}

html[theme="light"]{
  --page-color: #e8e8e8;
  --shade-1: #ececec;
  --box-shadow: -5px -5px 10px rgba(255, 255, 255, 1), 5px 5px 10px rgba(0, 0, 0, 0.2);
  --text-color: #414141;
  --box-shadow-inset: inset -2px -2px 5px rgb(255, 255, 255),
  inset 2px 2px 5px rgba(0, 0, 0, 0.15);
}

html[theme="dark"]{
  --page-color: rgb(28, 28, 28);
  --shade-1: rgb(30, 30, 30);
  --box-shadow: -3px -3px 6px rgba(255, 255, 255, 0.07), 5px 5px 10px rgba(0, 0, 0, 0.5);
  --text-color: white;
  --box-shadow-inset: inset 2px 2px 5px rgb(0, 0, 0, 0.7),
  inset -2px -2px 5px rgba(255, 255, 255, 0.15);
}

body {
  font-family: 'Roboto Mono', monospace;
  background-color: var(--page-color);

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.toggleLight-container{
  position: absolute;
  right: 15px;
  top: 15px;
  width: 40px;
  height: 40px;
  color: var(--icon-color);
  background: none;
  border: none;
}

.main-content{
  height: 500px;
  margin-top: 10vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.typing-box{
  width: 550px;
  width: var(--typing-box-width);
  height: 375px;
  height: var(--typing-box-height);
  border-radius: 8px;
  background-color: var(--shade-1);
  box-shadow: var(--box-shadow);
}

.typing-text{
  padding: 20px;
  position: relative;
  width: calc(550px - 40px);
  width: calc(var(--typing-box-width) - 40px);
  height: auto;
  font-size: 20px;
  color: var(--text-color);
}

html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 100ms !important;
  transition-delay: 0 !important;
}
